import React, { Component } from 'react';
import { Link, Redirect } from 'react-router-dom';

import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';

class Login extends Component {

  state={
    email: '',
    password: '',
    ok:false,
  }



  valida = (campo) => {
    return (valor) => {
      this.setState({
        [campo]: valor.target.value,
      })
    }
  }

  login=(e)=>{
    e.preventDefault();
    console.log(this.state)
    const {email, password} = this.state;
        console.log(email, password)
        return fetch('https://peerpeel.herokuapp.com/v1/login/client',{
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                email,
                password,
            })
        })
        .then(res => res.json())
        .then(json =>{
            localStorage.setItem('token', json.token);
            this.setState({ok: true})
            return (<Redirect push to='/dashboard' />);
        })
        .catch(err => console.error(err));
  }
  render() {
    const { ok } = this.state;
    if(ok){
      return (<Redirect push to='/forms' />);
    }
    return (
      <div className="app flex-row align-items-center" style={{overflowY: 'scroll'}}>
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <img src={require('../../../assets/img/LogoColor.png')} style={{width:'100%'}}/>
                      <br/><br/>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="username" onChange={this.valida('email')} />
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Password" autoComplete="current-password" onChange={this.valida('password')}/>
                      </InputGroup>
                        <Col  style={{width:"100%"}} >
                          <Button  style={{width:"100%", justifyContent:'center', alignItems:'center', display:'flex'}} color="primary" className="px-4" onClick={(e)=>this.login(e)}>Login</Button>
                        <Link to="/register">
                        <Button  style={{width:"100%"}} color="primary" className="mt-2 d-block d-sm-none " active tabIndex={-1}>Register Now!</Button>
                      </Link>
                          <Button color="link" className="px-0">Forgot password?</Button>
                      </Col>
                    </Form>
                  </CardBody>
                </Card>
                <Card className="text-white bg-primary py-4  d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                      <Link to="/register">
                        <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Login;
